.swiper {
  &-container {
    width: 100%;
    padding-bottom: 25px;
  }

  &-pagination {
    position: relative;
    display: flex;
    align-items: center;

    &-bullet {
      opacity: 1;
      outline: none;
      width: 15px;
      height: 15px;
      margin: 0 !important;
      margin-right: 15px !important;
      background-color: #C8C8C8;

      &:last-of-type {
        @include respond(medium) {
          margin-right: 40px !important;
        }
        @include respond(max-small) {
          margin-right: 0 !important;
        }
      }

      &-active {
        background-color: #CD084B;
      }
    }
  }
}