// LEADS :: START

.leads {
  overflow: hidden;
  border-bottom: 1px solid #D8D8D8;

  .c-grid-fluid {
    @include respond(medium) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__wrapper {
    z-index: 1;
    position: relative;
    padding-top: 99px;
    padding-bottom: 7px;
    @include respond(max-small) {
      padding-top: 50px;
    }
  }

  &__row {
    display: flex;
    @include respond(max-small) {
      flex-direction: column-reverse;
      flex-wrap: wrap;
    }

    &-left,
    &-right {
      width: 50%;
      @include respond(max-small) {
        width: 100%;
      }
    }

    &-right {
      padding-left: 60px;
      padding-right: 10px;
      @include respond(max-small) {
        padding-left: 0;
      }
    }
  }

  &__text {
    text-align: left;

    &-wrapper {
      width: 100%;
      max-width: 450px;
      margin-right: auto;
      padding-top: 62px;
      @include respond(max-small) {
        max-width: 100%;
        padding-top: 0;
        margin-bottom: 30px;
      }
    }

    &--pb {
      padding-bottom: 30px;

      &:last-of-type {
        padding-bottom: 39px;
      }
    }
  }

  &__btn {
    max-width: 232px;
  }

  &__img {
    display: block;
    max-width: 100%;
    height: auto;
    margin-right: auto;
    @include respond(max-small) {
      position: relative;
      left: -15px;
    }
  }
}

// LEADS :: END