// SECTION plans :: START

.plans {
  @include fluid(padding-top, 320px, 1439px, 25px, 50px);
  @include fluid(padding-bottom, 320px, 1439px, 75px, 71px);

  &__wrapper {
    display: none;
  }

  &__title {
    text-align: center;
    line-height: 1;
    font-weight: 400;
    font-family: "Muli", sans-serif;
    @include fluid(font-size, 320px, 1600px, 30px, 40px);
    @include fluid(padding-bottom, 320px, 1439px, 40px, 80px);
  }

  &__block {
    display: flex;
    align-items: flex-start;
    @include respond(max-extra-small) {
      flex-wrap: wrap;
    }

    &-3,
    &-5 {
      li {
        &:last-of-type {
          p {
            color: $c-black;
          }
        }
      }
    }

    &-wrapper {
      display: flex;
      flex-direction: column;

      > div {
        min-height: 150px;
        padding-bottom: 50px;
        margin-bottom: 50px;
        border-bottom: 1px solid #D8D8D8;

        &:last-of-type {
          padding-bottom: 0;
          margin-bottom: 0;
          border: none;
        }
      }
    }

    &-left {
      width: 100%;
      max-width: calc(100% - 252px);
      padding-right: 30px;
      @include respond(max-small) {
        max-width: calc(100% - 185px);
        padding-right: 15px;
      }
      @include respond(max-extra-small) {
        max-width: 100%;
      }
    }

    &-right {
      width: 252px;
      padding-right: 67px;
      @include respond(max-small) {
        width: 185px;
        padding-right: 0;
      }
      @include respond(max-extra-small) {
        width: 100%;
      }
    }

    &-title {
      display: flex;
      align-items: center;
      padding-bottom: 21px;
      font-weight: 600;
      @include fluid(font-size, 320px, 767px, 22px, 26px);
      @include respond(max-extra-small) {
        flex-direction: column-reverse;
        text-align: center;
      }

      span {
        display: flex;
        align-items: center;
        height: 24px;
        margin-left: 19px;
        padding: 5px 15px;
        border-radius: 12px;
        background-color: #00449D;
        color: #fff;
        text-transform: uppercase;
        font-weight: bold;
        @include fluid(font-size, 320px, 767px, 12px, 14px);
        @include respond(max-extra-small) {
          margin-left: 0;
          margin-bottom: 5px;
        }
      }
    }

    ul {
      display: flex;
      flex-direction: column;

      li {
        display: flex;
        align-items: center;
        padding-bottom: 15px;

        &.is-hide {
          display: none;
        }

        > div {
          &:nth-of-type(1) {
            width: 30px;
          }

          &:nth-of-type(2) {
            width: 100%;
            max-width: calc(100% - 30px);
          }
        }

        svg {
          height: 16px;
          fill: #00449D;
        }

        p {
          color: #939393;
          @include fluid(font-size, 320px, 767px, 16px, 19px);
        }
      }
    }

    &-price {
      padding-bottom: 10px;
      font-weight: bold;
      @include fluid(font-size, 320px, 767px, 35px, 40px);
      @include respond(max-extra-small) {
        text-align: center;
      }
    }

    &-per {
      padding-bottom: 20px;
      @include fluid(font-size, 320px, 767px, 16px, 19px);
      @include respond(max-extra-small) {
        text-align: center;
      }
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 185px;
      height: 80px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 10px;
      border: 2px solid #CD084B;
      background-color: transparent;
      color: #CD084B;
      letter-spacing: 1.35px;
      text-transform: uppercase;
      font-size: 18px;
      font-weight: bold;
      @include transition(background-color, color);

      &:hover {
        background-color: #CD084B;
        color: $c-white;
        @include transition(background-color, color);
      }
    }
  }
}

// SECTION plans :: END